import React, { useEffect } from 'react'
import { Navbar, Nav, Form, FormControl, Button, Card, Row, Col, Container } from 'react-bootstrap';
import { useState } from 'react';
import health1 from '../imgs/blog/health1.png';
import food1 from '../imgs/blog/food1.jpg';
import sport1 from '../imgs/blog/sport1.png';
import health2 from '../imgs/blog/health2.jpg';
import food2 from '../imgs/blog/food2.jpg';
import sport2 from '../imgs/blog/sport2.jpg';
import health3 from '../imgs/blog/health3.jpg';
import food3 from '../imgs/blog/food3.jpg';
import sport3 from '../imgs/blog/sport3.jpg';

const Blog = () => {

  const [activeTopic, setActiveTopic] = useState('all');

  useEffect(()=>{
    setActiveTopic('all')
  },[])

  const data = [
    {activeTopic:"health",title:"Yoga for Athletes",text:" Yoga is often thought of as a gentle exercise for flexibility and relaxation, but it can also be ...",img:health1},
    {activeTopic:"health",title:"Tips for Practicing Self-Care",text:"Self-care is essential for maintaining physical and emotional well-being. This article will provide an  ...",img:health2},
    {activeTopic:"health",title:" Understanding the Impact of Chronic Stress",text:"Stress is a normal part of life, but chronic stress can have a negative impact on  ...",img:health3},
    {activeTopic:"sport",title:" The Science of Running",text:" Running is a popular form of exercise that has many benefits for overall health and wellness. ...",img:sport1},
    {activeTopic:"sport",title:"How to Develop a Winning Mindset",text:"A winning mindset is crucial for athletic success. This article will explore the psychological factors that  ...",img:sport2},
    {activeTopic:"sport",title:"Benefits of High-Intensity Interval Training",text:"High-Intensity Interval Training (HIIT) is a type of cardio training that alternates ...",img:sport3},
    {activeTopic:"food",title:"Seasonal Vegetables",text:"Eating a diet rich in vegetables can have many health benefits. ...",img:food1},
    {activeTopic:"food",title:"Top 10 Superfoods for Optimal Health",text:"Superfoods are nutrient-dense foods that are packed with vitamins, minerals, antioxidants ...",img:food2},
    {activeTopic:"food",title:"The Ultimate Guide to Meal Prep",text:"Meal prepping can be a great way to save time and ensure that you are eating healthy ...",img:food3},
  ]

  return (
    <div className='layout'>

      <Navbar id="basic-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link className={activeTopic === 'all' ? 'active' : ''} href="#all" onClick={() => setActiveTopic('all')}>View All</Nav.Link>
          <Nav.Link href="#health" onClick={() => setActiveTopic('health')}>Health</Nav.Link>
          <Nav.Link href="#sport" onClick={() => setActiveTopic('sport')}>Sport</Nav.Link>
          <Nav.Link href="#food" onClick={() => setActiveTopic('food')}>Food</Nav.Link>
        </Nav>
      </Navbar>

      <div className='w-100 m-x d-flex justify-content-between align-items-center'>
        <Row className='w-100 card-container'>
          {data.map((blog)=>{ return (activeTopic === 'all' || activeTopic === blog.activeTopic ?
              <Col className='mb-3' xs={12} sm={6} md={4} lg={4}>
              <Card className="mt-5 mt-md-0 w-100" style={{margin:'auto' }}>
              <div style={{backgroundImage:`url(${blog.img})`,height:'100%',backgroundSize: "cover",height:"35vh"}}></div>
                <Card.Body>
                  <Card.Title>{blog.title}</Card.Title>
                  <Card.Text>
                  {blog.text}
                  </Card.Text>
                  <Button className='btn-app btn-blog' href="#">Continue reading</Button>
                </Card.Body>
              </Card> 
              </Col> : null )
            })}
        </Row>
      </div>
  
    </div>
  )
}

export default Blog







